export default {
	data() {
		return {
			inputBottom: 0
		}
	},
	methods: {
		inputFocus(e) {
			let that = this
			that.inputBottom = e.detail.height
		},
		inputBlur() {
			let that = this
			that.inputBottom = 0
		}
	}
}
